import UserStatus from "~/constants/UserStatus";
import UserTypes from "~/constants/UserTypes";
import api from "~/services/api";

export default {
    name: "index",
    layout : "main-content",
    data(){
        return {
            UserStatus,
            UserTypes,
            users : []
        }
    },
    async created(){
        let res = await api.get('user/get',this.$cookies.get('token'))
        this.users = res.data.data.data
    }
}
